* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  /* align-items: center; */
  justify-content: center;
  background-color: rgba(210, 238, 236, 0.97);
  background-image: url("https://images.unsplash.com/photo-1572731013456-5ed911024bfa?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=400&q=80");
  background-repeat: no-repeat;
  background-size: cover;
  background-blend-mode: overlay;
  /* background-image: url("https://www.transparenttextures.com/patterns/climpek.png"); */
  font-family: "Open Sans", sans-serif;
  min-height: 100vh;
}

.wrapper {
  margin: 1.5rem auto 2rem;
  max-width: 80vw;
  width: 1100px;
}

.wrapper h3 {
  font-size: 1.1rem;
  text-align: center;
  letter-spacing: 1px;
}

.main-heading {
  font-size: 2.5rem;
  margin-bottom: 15px;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.sub-heading {
  font-size: 2rem;
  margin-bottom: 10px;
}

.tours-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 50px;
  width: 100%;
}

.each-tour {
  background-color: #ddd;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.8);
  margin: 0 auto 3rem;
  width: 37rem;
}

.image-wrapper img {
  height: 450px;
  width: 100%;
}

.text-content {
  padding: 15px 20px;
}

.intro {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 15px;
}

.intro h4 {
  letter-spacing: 2px;
}

.intro .price {
  background-color: rgb(250, 194, 244);
  color: rgb(78, 6, 72);
  padding: 3px 9px;
  margin-left: 10px;
  text-align: center;
}

.remove-tour {
  display: flex;
  align-items: center;
  justify-content: center;
}

.remove-tour button {
  background-color: transparent;
  border-radius: 4px;
  border: 2px solid rgb(194, 19, 19);
  color: rgb(194, 19, 19);
  cursor: pointer;
  padding: 6px 15px;
}

.remove-tour button:focus,
.remove-tour button:active {
  outline: none;
}

.description p {
  color: rgb(85, 84, 84);
  font-family: "Poppins", sans-serif;
  font-size: 13px;
  letter-spacing: 1px;
  margin-bottom: 15px;
}

.description button {
  background-color: transparent;
  border: 1px solid transparent;
  color: rgb(19, 130, 194);
  margin-left: 2px;
  outline: none;
}

.button {
  background-color: transparent;
  border-radius: 2px;
  border: 1px solid rgb(3, 22, 75);
  color: rgb(3, 22, 75);
  cursor: pointer;
  font-weight: 600;
  padding: 4px 8px;
}

.button:hover {
  background-color: rgb(137, 207, 248);
}

@media screen and (max-width: 720px) {
  .each-tour {
    width: 80vw;
  }

  .image-wrapper img {
    height: 350px;
  }
}

